h1 {
  margin: 2rem 2rem 7rem;
  font-size: 3rem;
  font-weight: 700;
}

.log-in-container {
  width: 40%;
  text-align: center;
  color: #000;
  background-color: #fff;
  border: .3rem solid #ffb300;
  border-radius: 8%;
  margin: 15rem 5rem 5rem;
  position: relative;
  box-shadow: 0 0 .5rem #0000003d;
}

#create-acc-btn, #log-in-continue-btn {
  place-self: center;
}

#bg {
  width: 100%;
  height: 96rem;
  justify-content: center;
  align-items: center;
  margin-top: 15rem;
  display: flex;
}

.form-message {
  color: red;
  margin: 2rem;
  font-size: 1.6rem;
  font-weight: 700;
}

#log-in-section {
  width: 100%;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.grid label {
  font-size: 1.6rem;
  font-weight: 500;
  display: block;
}

.grid input {
  width: 50%;
  border: .1rem solid #00000080;
  border-radius: .9rem;
  margin-left: 25%;
  padding: 1.2rem;
  font-family: inherit;
  font-size: 1.3rem;
}

.sign-up-flex {
  width: 100%;
  height: auto;
  justify-content: center;
  align-items: center;
  margin-top: 15rem;
  display: flex;
  position: relative;
}

.sign-up-bg {
  width: 100%;
  height: 110rem;
  background-image: linear-gradient(45deg, #51cf66, #ffb300);
  justify-content: center;
  align-items: center;
  margin-top: 15rem;
  display: flex;
  position: absolute;
  overflow: hidden;
}

.sign-up-hyperlink:link, .sign-up-hyperlink:visited {
  color: #5d27da;
  border-bottom: .1rem solid #5d27da;
  margin: 0 .5rem .5rem;
  font-size: 1.2rem;
  text-decoration: none;
}

.sign-up-hyperlink:hover, .sign-up-hyperlink:active {
  color: #df11ce;
  border-bottom: .1rem solid #df11ce;
}

.sign-up-hyperlink-container {
  grid-column: 2;
  display: flex;
}

.sign-up-hyperlink-container p {
  color: #000;
  font-size: 1.2rem;
  font-weight: 500;
}

.continue-as-guest-hyperlink:link, .continue-as-guest-hyperlink:visited {
  color: #5d27da;
  border-bottom: .1rem solid #5d27da;
  grid-area: 5 / 2;
  justify-self: center;
  margin: 0 .5rem .5rem;
  font-size: 1.2rem;
  text-decoration: none;
}

.continue-as-guest-hyperlink:hover, .continue-as-guest-hyperlink:active {
  color: #df11ce;
  border-bottom: .1rem solid #df11ce;
}

.hyperlink-grid {
  grid-template-rows: repeat(6, 1fr);
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  align-items: center;
  display: grid;
}

.or-text {
  color: #000;
  grid-area: 3 / 2;
  justify-self: center;
  font-size: 1.2rem;
  font-weight: 500;
}

